import React from 'react'
import { styled } from '@mui/material/styles';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

const ElementRow = ({ element }) => {

  let included;

  if (element.included === "true") {
    included = <StyledTableCell align="right">{element.team}</StyledTableCell>
  } else if (element.included === 'eliminated') {
    included = <StyledTableCell sx={{ color: "#ff0000", textDecoration: "line-through"}} align="right">{element.owner}</StyledTableCell>
  } else if (element.included && element.flag) {
    included = <StyledTableCell align="right">{element.owner}<img src={element.flag} alt='homeflag' width="24vw" height="15vh"/></StyledTableCell>
  } else {
    included = <StyledTableCell align="right">{element.owner}</StyledTableCell>
  }

  return (
    <StyledTableRow key={element.owner}>
      {included}
      {/* <StyledTableCell align="right">{element.team}</StyledTableCell> */}
      <StyledTableCell align="right">{element.played}</StyledTableCell>
      <StyledTableCell align="right">{element.win}</StyledTableCell>
      <StyledTableCell align="right">{element.draw}</StyledTableCell>
      <StyledTableCell align="right">{element.loss}</StyledTableCell>
      <StyledTableCell align="right">{element.total_points}</StyledTableCell>
    </StyledTableRow>
  )
}

export default ElementRow