import React from 'react';
import row from './userData';
import Box from '@mui/material/Box';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';


function Row(props) {
  const { row } = props;
  const [open, setOpen] = React.useState(false);

  let included;

  if (row.flag !== undefined) {
    included = <TableCell width="90%" sx={{ borderBottom: "none", display: 'inline-block'}}>{row.owner} <img src={row.flag} alt='homeflag' width="24vw" height="15vh"/></TableCell>
  } else {
    included = <TableCell sx={{ borderBottom: "none"}} align="right">{row.owner}</TableCell>
  }

  return (
    <React.Fragment>
      <TableRow sx={{ '& > *': { borderBottom: 'unset' } }}>
        <TableCell width="1em">
          <IconButton
            aria-label="expand row"
            size="small"
            onClick={() => setOpen(!open)}
          >
            {open ? <KeyboardArrowUpIcon style={{ fontSize: '1rem' }}/> : <KeyboardArrowDownIcon style={{ fontSize: '1rem' }}/>}
          </IconButton>
        </TableCell>
        <TableCell component="th" scope="row">
          {included}
          {/* {row.owner} */}
          {/* {(row.pic = false) ? "Not Available" : row.owner} */}
        </TableCell>
        <TableCell align="right">{row.played}</TableCell>
        <TableCell align="right">{row.win}</TableCell>
        <TableCell align="right">{row.draw}</TableCell>
        <TableCell align="right">{row.loss}</TableCell>
        <TableCell align="right">{row.total_points}</TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ margin: 1 }}>
              <Typography variant="h6" gutterBottom component="div">
                Teams
              </Typography>
              <Table size="small" aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell sx={{ borderBottom: "none"}}>{row.teamA}</TableCell>
                    <TableCell sx={{ borderBottom: "none"}}>{row.teamB}</TableCell>
                    <TableCell sx={{ borderBottom: "none"}}>{row.teamC}</TableCell>
                    <TableCell sx={{ borderBottom: "none"}}>{row.teamD}</TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

export default function CollapsibleTable() {
  return (
    <TableContainer component={Paper}>
      <Table aria-label="collapsible table">
        <TableHead sx={{ "& th": {backgroundColor: "#295aa8", color: "white"}}}>
          <TableRow>
            <TableCell />
            <TableCell>Player</TableCell>
            <TableCell align="right">Played</TableCell>
            <TableCell align="right">Won</TableCell>
            <TableCell align="right">Draw</TableCell>
            <TableCell align="right">Lost</TableCell>
            <TableCell align="right">Total</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {row.map((row) => (
            <Row key={row.owner} row={row} />
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}